.x-select-item {
  margin: 0;
  padding: 5px 15px;
  clear: both;
  font-size: 12px !important;
  white-space: nowrap;
  list-style: none;
  cursor: pointer;
  box-sizing: border-box;
  transition: background 0.2s ease-in-out;
}
.x-select-item:hover {
  background: #f3f3f3;
}
.x-select-item--focus {
  background: #f3f3f3;
}
.x-select-item--disabled {
  cursor: not-allowed;
}
.x-select-item--disabled:hover {
  cursor: not-allowed;
}
.x-select-item--divided {
  margin-top: 5px;
  border-top: 1px solid #e5e5e5;
}
.x-select-item--divided:before {
  content: '';
  height: 5px;
  display: block;
  margin: 0 -16px;
  background-color: #fff;
  position: relative;
  top: -7px;
}
.x-select-large .x-select-item {
  padding: 7px 16px 8px;
  font-size: 13px !important;
}
.theme-light .x-select-item {
  color: #383838;
}
.theme-light .x-select-item:hover {
  background: #f4f4f4;
}
.theme-light .x-select-item--focus {
  background: #f4f4f4;
}
.theme-light .x-select-item--disabled {
  color: #c3c3c3;
}
.theme-light .x-select-item--disabled:hover {
  color: #c3c3c3;
  background-color: #f7f7f7;
}
.theme-light .x-select-item--selected,
.theme-light .x-select-item--selected:hover {
  color: #fff;
  background: #c0252e;
}
.theme-light .x-select--multiple .x-select-item--selected {
  color: #c0252e;
  background: #fff;
}
.theme-light .x-select--multiple .x-select-item--focus,
.theme-light .x-select--multiple .x-select-item:hover {
  background: #f7f7f7;
}
.theme-light .x-select--multiple .x-select-item--selected:after {
  color: #c0252e;
}
.theme-dark .x-select-item {
  color: #999;
}
.theme-dark .x-select-item:hover {
  background: #323232;
}
.theme-dark .x-select-item--focus {
  background: #323232;
}
.theme-dark .x-select-item--disabled {
  color: #8a8a8a;
}
.theme-dark .x-select-item--disabled:hover {
  color: #8a8a8a;
  background-color: #383838;
}
.theme-dark .x-select-item--selected,
.theme-dark .x-select-item--selected:hover {
  color: #fff;
  background: #c0252e;
}
.theme-dark .x-select--multiple .x-select-item--selected {
  color: #c0252e;
  background: #383838;
}
.theme-dark .x-select--multiple .x-select-item--focus,
.theme-dark .x-select--multiple .x-select-item:hover {
  background: #2d2d2d;
}
.theme-dark .x-select--multiple .x-select-item--selected:after {
  color: #c0252e;
}
.x-select-item .after {
  display: none;
}
.x-select--multiple .x-select-item--selected .after {
  display: inline-block;
  float: right;
}
/*# sourceMappingURL=src/components/select/option.css.map */


