.theme-light .x-cascader-panel__options {
  color: #383838;
  background-color: #fff;
  border-right: 1px solid #e5e5e5;
}
.theme-light .x-cascader-panel__options-item:hover {
  background: #f4f4f4;
}
.theme-light .x-cascader-panel__options-item--selected {
  color: #c0252e;
  background: #f4f4f4;
}
.theme-dark .x-cascader-panel__options {
  color: #999;
  background-color: #383838;
  border-right: 1px solid #4c4c4c;
}
.theme-dark .x-cascader-panel__options-item:hover {
  background: #323232;
}
.theme-dark .x-cascader-panel__options-item--selected {
  color: #c0252e;
  background: #383838;
}
.x-cascader-panel {
  display: inline-block;
}
.x-cascader-panel__options {
  display: inline-block;
  min-width: 100px;
  height: 180px;
  margin: 0;
  vertical-align: top;
  box-sizing: border-box;
}
.x-cascader-panel__options:last-child {
  border: none;
}
.x-cascader-panel__options-item {
  position: relative;
  padding: 5px 10px;
  cursor: pointer;
}
.x-cascader-panel__options-item .icon {
  position: absolute;
  right: 4px;
  top: 8px;
}
.x-cascader-panel__options-item--has-children .label {
  margin-right: 10px;
}
