







































































































@import '../../styles/common'

.x-modal
  fixed left top
  size 100%
  z-index zindex-modal
  display table

  &-wrapper
    display table-cell
    vertical-align middle

  &-dialog
    position relative
    margin 0 auto
    width 380px
    min-height 70px
    border-radius border-radius-base

  &-close
    absolute right top
    cursor pointer
    size 30px
    text-align center
    line-height 30px
    transition color ease transition-time

  &__header
    padding 8px 15px

    h3
      font-weight normal
      margin 0

  &__body
    box-sizing border-box
    padding 15px

  &__footer
    padding 0 15px 15px
    text-align right

for theme in themes
  .theme-{theme}
    .x-modal
      background lookup(theme + '-modal-mask-bg')
      &-dialog
        background lookup(theme + '-modal-dialog-bg')
        box-shadow lookup(theme + '-modal-dialog-shadow')
      &-close
        color lookup(theme + '-close-default-color')
        &:hover
          color lookup(theme + '-close-hover-color')



