





































@import '../../styles/common'

// 
.x-tabs
  padding 0 15px
  border-bottom-width 1px
  border-bottom-style solid

  ul
    list-style none
    margin 0
    padding 0
    font-size 0

  li
    display inline-block
    font-size 13px
    padding 0 20px
    line-height 28px
    cursor pointer
    box-sizing border-box

    &:hover
      text-decoration none

    &.router-link-active
      height 29px
      border-width 1px
      border-style solid
      border-bottom none!important
      position relative
      bottom -1px
      font-weight bold

  &--line
    li
      &.router-link-active
        border none

        &:after
          absolute left bottom
          size 100% 3px
          content ''

for theme in themes
  .theme-{theme}
    .x-tabs
      border-bottom-color lookup(theme + '-tab-border-color')
      li
        color lookup(theme + '-tab-item-color')
        &:hover
          color lookup(theme + '-tab-item-hover-color')
        &.router-link-active
          background-color lookup(theme + '-body-bg')
          border-color lookup(theme + '-tab-border-color')

      &--line
        li
          &.router-link-active
            &:after
              background-color lookup(theme + '-tab-item-active-line-bg')



