for theme in themes
  .theme-{theme}
    .x-select-dropdown
      background-color lookup(theme + '-select-item-bg')

.x-select-dropdown
  width inherit
  max-height 200px
  overflow auto
  margin 5px 0
  padding 5px 0
  background-color #FFF
  box-sizing border-box
  border-radius border-radius-small
  box-shadow shadow-base
  position absolute
  z-index zindex-select
  user-select none



