/*@import '../../assets/stylus/common'*/
.x-tree-1 {
  width: 100%;
  height: 100%;
  overflow: auto;
  position: relative;
}
.x-tree-1 .list .tree-content {
  position: relative;
  padding-left: 20px;
  box-sizing: border-box;
}
.x-tree-1 .list .tree-content .tree-content-label {
  white-space: nowrap;
}
.x-tree-1 .list .tree-content span,
.x-tree-1 .list .tree-content i {
  cursor: pointer;
}
.x-tree-1 .list .tree-content .edit-input {
  position: absolute;
  left: 20px;
  width: 100px;
}
.x-tree-1 .list .tree-content .line {
  position: absolute;
  height: 0;
  border-top: 1px solid #aaa;
  top: 50%;
  transform: translate(0, -1px);
}
.x-tree-1 .list .tree-content .vertical-line {
  position: absolute;
  width: 0;
  border-left: 1px solid #aaa;
  height: 10px;
  bottom: 50%;
}
.x-tree-1 .list.selected {
  background: #f3f3f3;
}
.x-tree-1 .list.selected .icon {
  background: #f3f3f3;
}
.x-tree-1 .icon {
  width: 10px;
  height: 15px;
  background: #fff;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  z-index: 1;
}
.x-tree-1 .hide-input {
  width: 100%;
  height: 100%;
  opacity: 0.3;
  position: absolute;
  left: -9999px;
}
/*# sourceMappingURL=src/components/tree/tree.css.map */


