










































































@import '../../styles/common'
@import '../../styles/mixins/button'

for theme in themes
  .theme-{theme}
    .x-btn {
      btn(theme)
      btn-default(theme)
      &--primary {
        btn-primary(theme)
      }

      &--ghost {
        btn-ghost(theme)
      }

      &--dashed {
        btn-dashed(theme)
      }

      &--text {
        btn-text(theme)
      }

      &--success {
        btn-full-fill(theme, lookup(theme + '-btn-success-color'))
      }

      &--warning {
        btn-full-fill(theme, lookup(theme + '-btn-warning-color'))
      }

      &--error {
        btn-full-fill(theme, lookup(theme + '-btn-error-color'))
      }
    }

.x-btn {
  &--long {
    width: 100%;
  }

  & > .{icon-prefix} + span,
  & > span + .{icon-prefix} {
    margin-left: 4px;
  }
}



