





























































































































































































































































































































































































































































































































































































































































@import '../../styles/common'
@import '../../styles/mixins/inner-icon'
@import '../../styles/mixins/placeholder'

for theme in themes
  .theme-{theme}
    .x-select
      color lookup(theme + '-select-color')

      &__trigger
        background-color lookup(theme + '-select-bg')
        border 1px solid lookup(theme + '-select-border-color')

      &:hover
        border-color lookup(theme + '-select-hover-border-color')

      &__icon
        color lookup(theme + '-select-icon-color')

      &--visible
        .x-select__trigger
          border-color lookup(theme + '-select-active-border-color')

      &--disabled
        .x-select__trigger
          background-color lookup(theme + '-select-disabled-bg')
          color lookup(theme + '-select-disabled-color')
          &:hover
            border-color lookup(theme + '-select-disabled-hover-border-color')

      &--single,
      &--multiple
        .x-select__placeholder
          color lookup(theme + '-select-placeholder-color')

      // 
      &__input
        color lookup(theme + '-select-color')
        background-color lookup(theme + '-select-bg')
        placeholder(lookup(theme + '-select-placeholder-color'))

    .x-select-tag
      border 1px solid lookup(theme + '-select-tag-border-color')
      background lookup(theme + '-select-tag-bg')

    .x-select-tag .icon
        color lookup(theme + '-select-icon-color')

// 
.x-select
  display inline-block
  width 100%
  box-sizing border-box
  vertical-align middle
  font-size font-size-base
  line-height normal

  &__trigger
    display block
    box-sizing border-box
    outline none
    user-select none
    cursor pointer
    position relative
    border-radius border-radius-base
    transition all transition-time ease-in-out

    .x-select__icon:nth-of-type(1)
      display none
      cursor pointer

    &:hover
      .x-select__icon:nth-of-type(1)
        display inline-block

  &--show-clear &__trigger:hover .x-select__icon:nth-of-type(2)
    display none

  &__icon
    inner-icon()

  &--visible
    .x-select__trigger
      outline 0
    .x-select__icon:nth-of-type(2)
      transform rotate(180deg)

  &--disabled
    .x-select__trigger
      opacity 1
      cursor not-allowed

      .x-select__icon:nth-of-type(1)
        display none

      &:hover
        box-shadow none

        .x-select__icon:nth-of-type(2)
          display inline-block

  &--single &__trigger
    height input-height-base
    position relative

    .x-select__placeholder, .x-select__selected-value
      display block
      height input-height-base - 2px
      line-height input-height-base - 2px
      font-size font-size-small
      overflow hidden
      text-overflow ellipsis
      white-space nowrap
      padding-left 8px
      padding-right 24px

  &--large&--single &__trigger
    height input-height-large

    .x-select__placeholder, .x-select__selected-value
      height input-height-large - 2px
      line-height input-height-large - 2px
      font-size font-size-base

  &--small&--single &__trigger
    height input-height-small
    border-radius btn-border-radius-small

    .x-select__placeholder, .x-select__selected-value
      height input-height-small - 2px
      line-height input-height-small - 2px

  &--multiple &__trigger
    padding 0 24px 0 4px
    min-height input-height-base

    .x-select__placeholder
      display block
      height input-height-base - 2px
      line-height input-height-base - 2px
      font-size font-size-small
      overflow hidden
      text-overflow ellipsis
      white-space nowrap
      padding-left 4px
      padding-right 22px

  // 
  &__input
    display inline-block
    height input-height-base - 2
    line-height input-height-base
    padding 0 24px 0 8px
    font-size font-size-small
    outline none
    border none
    box-sizing border-box
    position relative
    cursor pointer

  &--single &__input
    width 100%

  &--large &__input
    font-size font-size-base
    height input-height-large

  &--small &__input
    height input-height-small

  &--multiple &__input
    height input-height-base - 3px
    line-height input-height-base
    padding 0 0 0 4px

  &__not-found
    text-align center
    color btn-disable-color

  &--multiple .{css-prefix}-tag
    margin 3px 4px 2px 0

// 
.x-select-tag
  display inline-block
  height 22px
  line-height 22px
  margin 2px 4px 2px 0
  padding 0 8px
  border-radius btn-border-radius-small
  font-size font-size-small
  vertical-align middle
  opacity 1
  overflow hidden
  cursor pointer



