.x-tooltip {
  display: inline-block;
/*&__rel
    display inline-block
    position relative*/
}
.x-tooltip__popper {
  z-index: 1060;
}
.x-tooltip__inner {
  max-width: 300px;
  min-height: 20px;
  padding: 8px 12px;
  text-align: left;
  text-decoration: none;
  border-radius: 3px;
  white-space: nowrap;
}
.x-tooltip__inner p {
  margin: 5px 0;
}
.theme-light .x-tooltip__popper {
  display: block;
  visibility: visible;
  font-size: 12px;
  line-height: 1.5;
  position: absolute;
}
.theme-light .x-tooltip__popper .x-popper__arrow {
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
}
.theme-light .x-tooltip__popper[x-placement^="top"] {
  padding: 5px 0 8px 0;
}
.theme-light .x-tooltip__popper[x-placement^="right"] {
  padding: 0 5px 0 8px;
}
.theme-light .x-tooltip__popper[x-placement^="bottom"] {
  padding: 8px 0 5px 0;
}
.theme-light .x-tooltip__popper[x-placement^="left"] {
  padding: 0 8px 0 5px;
}
.theme-light .x-tooltip__popper[x-placement^="top"] .x-popper__arrow {
  bottom: 3px;
  border-width: 5px 5px 0;
  border-top-color: rgba(76,76,76,0.9);
}
.theme-light .x-tooltip__popper[x-placement="top"] .x-popper__arrow {
  left: 50%;
  margin-left: -5px;
}
.theme-light .x-tooltip__popper[x-placement="top-start"] .x-popper__arrow {
  left: 16px;
}
.theme-light .x-tooltip__popper[x-placement="top-end"] .x-popper__arrow {
  right: 16px;
}
.theme-light .x-tooltip__popper[x-placement^="right"] .x-popper__arrow {
  left: 3px;
  border-width: 5px 5px 5px 0;
  border-right-color: rgba(76,76,76,0.9);
}
.theme-light .x-tooltip__popper[x-placement="right"] .x-popper__arrow {
  top: 50%;
  margin-top: -5px;
}
.theme-light .x-tooltip__popper[x-placement="right-start"] .x-popper__arrow {
  top: 8px;
}
.theme-light .x-tooltip__popper[x-placement="right-end"] .x-popper__arrow {
  bottom: 8px;
}
.theme-light .x-tooltip__popper[x-placement^="left"] .x-popper__arrow {
  right: 3px;
  border-width: 5px 0 5px 5px;
  border-left-color: rgba(76,76,76,0.9);
}
.theme-light .x-tooltip__popper[x-placement="left"] .x-popper__arrow {
  top: 50%;
