.theme-light .x-tooltip__popper[x-placement^="bottom"] .x-popper__arrow {
  top: 3px;
  border-width: 0 5px 5px;
  border-bottom-color: rgba(76,76,76,0.9);
}
.theme-light .x-tooltip__popper[x-placement="bottom"] .x-popper__arrow {
  left: 50%;
  margin-left: -5px;
}
.theme-light .x-tooltip__popper[x-placement="bottom-start"] .x-popper__arrow {
  left: 16px;
}
.theme-light .x-tooltip__popper[x-placement="bottom-end"] .x-popper__arrow {
  right: 16px;
}
.theme-light .x-tooltip__inner {
  color: #fff;
  background-color: rgba(76,76,76,0.9);
}
