@import '../../styles/mixins/popper'

.x-tooltip
  display inline-block

  /*&__rel
    display inline-block
    position relative*/

  &__popper
    z-index zindex-tooltip

  &__inner
    max-width tooltip-max-width
    min-height 20px
    padding 8px 12px
    text-align left
    text-decoration none
    border-radius btn-border-radius-base
    white-space nowrap

    p
      margin 5px 0

for theme in themes
  .theme-{theme}
    .x-tooltip
      &__popper
        popper(tooltip-arrow-width, tooltip-distance, lookup(theme + '-tooltip-bg'))
      &__inner
        color lookup(theme + '-tooltip-color')
        background-color lookup(theme + '-tooltip-bg')



