

























































@import '../../styles/common'

.table-header-icon-root
  width 100%
  position absolute
  right 5px
  top 50%
  transform translateY(-50%)
  width 12px
  min-height 18px

  .icon-container
    position relative
    width 100%
    height 20px
    text-align center

  .sort-box
    cursor pointer
    .sort-icon
      position absolute
      top 50%
      left 50%
      transform translate(-50%, -50%) scale(1, 1.1)
      &.hide
        opacity 0.5

.center
  position absolute
  top 50%
  top 50%
  left 50%
  transform translate(-50%, -50%) scale(1, 1.1)

for theme in themes
  .theme-{theme}
    .table-header-icon-root
      .sort-icon
        color lookup(theme + '-table-header-icon-sort-icon-color')




