.x-spin {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1800;
}
.x-spin__spinner {
  position: absolute;
  top: 50%;
  width: 100%;
  text-align: center;
}
.circular {
  animation: rotate 2s linear infinite;
}
.path {
  animation: dash 1.5s ease-in-out infinite;
  stroke-dasharray: 1, 100;
  stroke-dashoffset: 0;
  stroke-width: 2;
  stroke-linecap: round;
}
.theme-light .x-spin--with-mask {
  background: rgba(255,255,255,0.75);
}
.theme-dark .x-spin--with-mask {
  background: rgba(255,255,255,0.2);
}
@-moz-keyframes rotate {
  to {
    transform: rotate(1turn);
  }
}
@-webkit-keyframes rotate {
  to {
    transform: rotate(1turn);
  }
}
@-o-keyframes rotate {
  to {
    transform: rotate(1turn);
  }
}
@keyframes rotate {
  to {
    transform: rotate(1turn);
  }
}
@-moz-keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  to {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}
@-webkit-keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  to {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}
@-o-keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  to {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}
@keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  to {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}
