<template>
  <div class="x-animate-text-root" :class="{'x-animate-text-hidden': !ready}" v-html="value"></div>
</template>

<script>
// : 
// : https://github.com/qgh810/animate-text
//
// 
// <animate-text
//   class="animate-text-number"
//   :value="123456"
//   :is-number="true"
//   :start-number="0"
//   :time="2000"
//   :change-count="50"
//   @animated="function () {console.log('')}">
//   >
// </animate-text>
import AnimateText from 'animate-text'

export default {
  name: 'AnimateText',

  props: {
    //  
    value: null,
    // 
    time: {
      type: Number,
      default: 500
    },
    // 
    isNumber: {
      type: Boolean,
      default: false
    },
    // 
    startNumber: {
      type: Number,
      default: 0
    },
    // 
    changeCount: {
      type: Number,
      default: 32
    }
  },

  data () {
    return {
      animateText: null,
      ready: false
    }
  },

  watch: {
    value (val) {
      this.ready = false
      setTimeout(() => {
        this.init()
      }, 30)
    },

    time (val) {
      this.animateText ? this.animateText.play(val) : this.init()
    }
  },

  mounted () {
    this.init()
  },

  methods: {
    init () {
      if (!this.value && this.value - 0 !== 0) return
      this.ready = true
      this.animateText = new AnimateText(this.$el, {
        time: this.time, // 
        isNumber: this.isNumber, // 
        startNumber: this.startNumber, //  
        changeCount: this.changeCount, // 
        onAnimated: this.onAnimated // 
      })
    },

    onAnimated () {
      this.$emit('animated')
    }
  }
}
</script>

<style lang="css" scoped>

.x-animate-text-hidden {
  opacity: 0
}

</style>



