.x-slider-component-root .slider .tooltip .triangle {
  size: 0 0;
  font-size: 0;
  overflow: hidden;
}
.x-slider-component-root {
  position: relative;
  width: 100%;
  padding: 1px;
  box-sizing: border-box;
  display: inline-block;
  transition: all ease 0.2s;
}
.x-slider-component-root.hide {
  opacity: 0;
}
.x-slider-component-root .not-allowed {
  cursor: not-allowed;
}
.x-slider-component-root .slider {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  opacity: 1;
/*&:hover
      opacity 1*/
}
.x-slider-component-root .slider .line-box {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 4px;
  top: -2px;
}
.x-slider-component-root .slider .line {
  position: absolute;
  width: 100%;
  height: 0px;
  border-top: 4px solid;
}
.x-slider-component-root .slider .circle {
  position: absolute;
  top: 0;
  left: 0;
  border: 2px solid;
  border-radius: 100px;
  box-sizing: border-box;
}
.x-slider-component-root .slider .circle.focus {
  box-shadow: 0 0 5px 0px;
}
.x-slider-component-root .slider .circle:hover {
  transform: scale(1.5);
}
.x-slider-component-root .slider .tooltip {
  position: absolute;
  left: 50%;
  bottom: 0;
  width: auto;
  transform: translate(-50%);
  background: rgba(0,0,0,0.5);
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 100;
  text-align: center;
  white-space: nowrap;
  z-index: 1060;
}
.x-slider-component-root .slider .tooltip .tooltip-input {
/*transform translate(10)*/
  margin: 0;
  padding: 0;
}
.x-slider-component-root .slider .tooltip .triangle {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%);
  border-width: 5px;
  border-style: solid dashed dashed;
  border-color: rgba(0,0,0,0.5) transparent transparent;
}
.x-slider-component-root>.label {
  position: absolute;
  bottom: 0;
}
.x-slider-component-root>.label.min {
  left: 0;
}
.x-slider-component-root>.label.max {
  right: 0;
}
.x-slider-component-root .hidden-input {
  position: absolute;
  top: -5000px;
  left: -5000px;
  width: 1px;
  height: 1px;
  opacity: 0;
}
.x-slider-component-root .pointer {
  cursor: pointer;
}
.x-slider-component-root.not-allowed,
.x-slider-component-root .not-allowed {
  cursor: not-allowed;
}
.x-slider-component-root.not-allowed .pointer,
.x-slider-component-root .not-allowed .pointer {
  cursor: not-allowed;
}
.x-slider-component-root .transition {
  transition: all ease-in-out 0.3s;
}
.no-user-select {
  user-select: none;
}
.theme-light .x-slider-component-root .line.back-line {
  color: #e9e9e9;
}
.theme-light .x-slider-component-root .circle {
  background: #fff;
}
.theme-light .x-slider-component-root .tooltip-input {
  color: #fff;
}
.theme-light .x-slider-component-root>.label {
  color: #383838;
}
.theme-light .x-slider-component-root .tooltip {
  color: #fff;
}
.theme-dark .x-slider-component-root .line.back-line {
  color: #e9e9e9;
}
.theme-dark .x-slider-component-root .circle {
  background: #fff;
}
.theme-dark .x-slider-component-root .tooltip-input {
  color: #fff;
}
.theme-dark .x-slider-component-root>.label {
  color: #999;
}
.theme-dark .x-slider-component-root .tooltip {
  color: #fff;
}
