.table-header-icon-root {
  width: 100%;
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  width: 12px;
  min-height: 18px;
}
.table-header-icon-root .icon-container {
  position: relative;
  width: 100%;
  height: 20px;
  text-align: center;
}
.table-header-icon-root .sort-box {
  cursor: pointer;
}
.table-header-icon-root .sort-box .sort-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1, 1.1);
}
.table-header-icon-root .sort-box .sort-icon.hide {
  opacity: 0.5;
}
.center {
  position: absolute;
  top: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1, 1.1);
}
.theme-light .table-header-icon-root .sort-icon {
  color: #888;
}
.theme-dark .table-header-icon-root .sort-icon {
  color: #888;
}
