.x-loading-bar-root {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  z-index: 2000;
}
.x-loading-bar-root .x-loading-bar {
  height: 2px;
  box-shadow: 1px 1px 2px 2px rgba(0,0,0,0.06);
  transition: all ease 0.2s;
}
