@import '../../styles/mixins/inner-icon'
@import '../../styles/mixins/placeholder'

for theme in themes
  .theme-{theme}
    .x-cascader
      color lookup(theme + '-cascader-color')

      &__trigger
        background-color lookup(theme + '-cascader-bg')
        border 1px solid lookup(theme + '-cascader-border-color')

        &:hover
        border-color lookup(theme + '-cascader-hover-border-color')

        .x-cascader__placeholder
          color lookup(theme + '-cascader-placeholder-color')

      &__icon
        color lookup(theme + '-cascader-icon-color')

.x-cascader
  display inline-block
  width 100%
  box-sizing border-box
  vertical-align middle
  font-size font-size-base
  line-height normal

  &__trigger
    height input-height-base
    display block
    box-sizing border-box
    outline none
    user-select none
    cursor pointer
    position relative
    border-radius border-radius-base
    transition all transition-time ease-in-out

    .x-cascader__icon:nth-of-type(1)
      display none
      cursor pointer

    &:hover
      .x-cascader__icon:nth-of-type(1)
        display inline-block

    .x-cascader__placeholder, .x-cascader__selected-value
      display block
      height input-height-base - 2px
      line-height input-height-base - 2px
      font-size font-size-small
      overflow hidden
      text-overflow ellipsis
      white-space nowrap
      padding-left 8px
      padding-right 24px

  &__icon
    inner-icon()

  &--large &__trigger
    height input-height-large

    .x-cascader__placeholder, .x-cascader__selected-value
      height input-height-large - 2px
      line-height input-height-large - 2px
      font-size font-size-base

  &--small &__trigger
    height input-height-small
    border-radius btn-border-radius-small

    .x-cascader__placeholder, .x-cascader__selected-value
      height input-height-small - 2px
      line-height input-height-small - 2px

  .x-select-dropdown
    width auto
    padding 0
    white-space nowrap
    overflow visible



