.x-grid-item {
  transition: all 200ms ease;
  transition-property: left, top, right;
/* add right for rtl */
}
.x-grid-item.css-transforms {
  transition-property: transform;
}
.x-grid-item.resizing {
  opacity: 0.6;
  z-index: 3;
}
.x-grid-item.x-draggable-dragging {
/*transition:none*/
  z-index: 3;
}
.x-grid-item.x-grid-placeholder {
  background: #f00;
  opacity: 0.2;
  transition-duration: 100ms;
  z-index: 2;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.x-grid-item > .x-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  bottom: 0;
  right: 0;
  background: url("data:image/svg+xml;base64,");
  background-position: bottom right;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  box-sizing: border-box;
  cursor: se-resize;
}
.x-grid-item > .x-rtl-resizable-handle {
  bottom: 0;
  left: 0;
  background: url("data:image/svg+xml;base64,");
  background-position: bottom left;
  padding-left: 3px;
  background-repeat: no-repeat;
  background-origin: content-box;
  cursor: sw-resize;
  right: auto;
}
