.theme-light .x-cascader {
  color: #383838;
}
.theme-light .x-cascader__trigger {
  background-color: #fff;
  border: 1px solid #dcdcdc;
}
.theme-light .x-cascader__trigger:hover,
.theme-light .x-cascader__trigger border-color lookup(theme + '-cascader-hover-border-color'),
.theme-light .x-cascader__trigger .x-cascader__placeholder {
  color: #383838;
}
.theme-light .x-cascader__icon {
  color: #999;
}
.theme-dark .x-cascader {
  color: #999;
}
.theme-dark .x-cascader__trigger {
  background-color: #383838;
  border: 1px solid #383838;
}
.theme-dark .x-cascader__trigger:hover,
.theme-dark .x-cascader__trigger border-color lookup(theme + '-cascader-hover-border-color'),
.theme-dark .x-cascader__trigger .x-cascader__placeholder {
  color: #999;
}
.theme-dark .x-cascader__icon {
  color: #999;
}
.x-cascader {
  display: inline-block;
  width: 100%;
  box-sizing: border-box;
  vertical-align: middle;
  font-size: 13px;
  line-height: normal;
}
.x-cascader__trigger {
  height: 32px;
  display: block;
  box-sizing: border-box;
  outline: none;
  user-select: none;
  cursor: pointer;
  position: relative;
  border-radius: 3px;
  transition: all 0.2s ease-in-out;
}
.x-cascader__trigger .x-cascader__icon:nth-of-type(1) {
  display: none;
  cursor: pointer;
}
.x-cascader__trigger:hover .x-cascader__icon:nth-of-type(1) {
  display: inline-block;
}
.x-cascader__trigger .x-cascader__placeholder,
.x-cascader__trigger .x-cascader__selected-value {
  display: block;
  height: 30px;
  line-height: 30px;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-left: 8px;
  padding-right: 24px;
}
.x-cascader__icon {
  position: absolute;
  top: 50%;
  right: 8px;
  line-height: 1;
  margin-top: -7px;
  font-size: 13px;
  transition: all 0.2s ease-in-out;
}
.x-cascader--large .x-cascader__trigger {
  height: 36px;
}
.x-cascader--large .x-cascader__trigger .x-cascader__placeholder,
.x-cascader--large .x-cascader__trigger .x-cascader__selected-value {
  height: 34px;
  line-height: 34px;
  font-size: 13px;
}
.x-cascader--small .x-cascader__trigger {
  height: 28px;
  border-radius: 2px;
}
.x-cascader--small .x-cascader__trigger .x-cascader__placeholder,
.x-cascader--small .x-cascader__trigger .x-cascader__selected-value {
  height: 26px;
  line-height: 26px;
}
.x-cascader .x-select-dropdown {
  width: auto;
  padding: 0;
  white-space: nowrap;
  overflow: visible;
}
