@import '../../styles/mixins/input'

.x-input
  input()
  &-wrapper
    display inline-block
    width 100%
    position relative
    vertical-align middle

  &-icon
    size 32px input-height-base
    line-height input-height-base
    text-align center
    font-size 16px
    color extra-light-gray
    absolute right
    z-index 1

    &:hover
      color primary-color

  &-icon + &
    padding-right 32px

  &-wrapper--large &-icon
    font-size 18px
    height input-height-large
    line-height input-height-large

  &-wrapper--small &-icon
    size 24px input-height-small
    font-size 14px
    line-height input-height-small

    + .x-input
      padding-right 24px

for theme in themes
  .theme-{theme}
    .x-input
      placeholder(lookup(theme + '-input-placeholder-color'))
      input-color(theme)

    .x-input-group
      input-group-color(theme)

.x-input-group
  input-group()



