



































































@import '../../styles/common'

$col
  position relative
  display block
  float left
  flex 0 0 auto
  box-sizing border-box
  &:first-child
    padding-left 0
  &:last-child
    padding-right 0

for i in 1..grid-columns
  .x-col-{i}
    @extend $col
    width ((100 / grid-columns) * i)%

  unless i is grid-columns
    .x-col--offset-{i}
      margin-left ((100 / grid-columns) * i)%

    .x-col--push-{i}
      left ((100 / grid-columns) * i)%

    .x-col--pull-{i}
      right ((100 / grid-columns) * i)%



