







































































@import '../../styles/common'

.x-breadcrumb
  padding 5px 15px 10px
  line-height 20px
  font-size 12px
  clearfix()

  a
  span
  i
    display inline-block

  &__back
    margin-right 5px
    font-size 16px

  &__link
    margin-right 5px

  &__seperator
    margin-right 5px

for theme in themes
  .theme-{theme}
    .x-breadcrumb
      &__back
        color lookup(theme + '-breadcrumb-back-color')
        &:hover
          color lookup(theme + '-breadcrumb-back-hover-color')
      &__link
        color lookup(theme + '-breadcrumb-link-color')
      &__seperator
        color lookup(theme + '-breadcrumb-seperator-color')



