





























































@import '../../styles/common'

.x-progress-root
  display inline-block
  width 100%
  height 6px
  .progress
    position relative
    height 100%
    overflow hidden
    transition all ease 0.2s
  .animation-mask
    position absolute
    top 0
    left 0
    width 100%
    height 100%
    background linear-gradient(left, rgba(255,255,255,0), rgba(255,255,255,0.3), rgba(255,255,255,0.3), rgba(255,255,255,0))
    animation play 3s infinite

  &.border-radius
    border-radius 100px
    .border-radius
      border-radius 100px


  @keyframes play
    from
      transform translateX(-100%)
    to
      transform translateX(100%)

for theme in themes
  .theme-{theme}
    .x-progress-root
      background lookup(theme + '-progress-bg-color')




