.x-modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  display: table;
}
.x-modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
.x-modal-dialog {
  position: relative;
  margin: 0 auto;
  width: 380px;
  min-height: 70px;
  border-radius: 3px;
}
.x-modal-close {
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  transition: color ease 0.2s;
}
.x-modal__header {
  padding: 8px 15px;
}
.x-modal__header h3 {
  font-weight: normal;
  margin: 0;
}
.x-modal__body {
  box-sizing: border-box;
  padding: 15px;
}
.x-modal__footer {
  padding: 0 15px 15px;
  text-align: right;
}
.theme-light .x-modal {
  background: rgba(0,0,0,0.4);
}
.theme-light .x-modal-dialog {
  background: #fff;
  box-shadow: 0 1px 5px rgba(0,0,0,0.3);
}
.theme-light .x-modal-close {
  color: #999;
}
.theme-light .x-modal-close:hover {
  color: #666;
}
.theme-dark .x-modal {
  background: rgba(255,255,255,0.2);
}
.theme-dark .x-modal-dialog {
  background: #141414;
  box-shadow: 0 1px 5px rgba(0,0,0,0.3);
}
.theme-dark .x-modal-close {
  color: #666;
}
.theme-dark .x-modal-close:hover {
  color: #999;
}
