.x-form {
  &--label-left {
    & .x-form-item__label {
      text-align: left
    }
  }
  &--label-top {
    & .x-form-item__label {
      float: none
      display: inline-block
      text-align: left
      padding: 0 0 10px 0
    }
  }
  &--inline {
    & .x-form-item {
      display: inline-block
      margin-right: 10px
      vertical-align: top
    }
    & .x-form-item__label {
      float: none
      display: inline-block
    }
    & .x-form-item__content {
      display: inline-block
      vertical-align: top
    }
    &.x-form--label-top .x-form-item__content {
      display: block
    }
  }
}
.x-form-item {
  margin-bottom: 22px
  clearfix()

  &__label {
    text-align: right
    vertical-align: middle
    float: left
    font-size: 14px
    line-height: 1
    padding: 11px 12px 11px 0
    box-sizing: border-box
  }
  &__content {
    line-height: 36px
    position: relative
    font-size: 14px
    clearfix()
  }
  &__error {
    font-size: 12px
    line-height: 1
    padding-top: 4px
    position: absolute
    top: 100%
    left: 0
  }

  &.is-required {
    .x-form-item__label:before {
      content: '*'
      margin-right: 4px
    }
  }
}

for theme in themes
  .theme-{theme}
    .x-form-item
      &__label
        color: lookup(theme + '-form-label-color')
      
      &__error
        color: lookup(theme + '-form-error-color')

      &.is-required {
        .x-form-item__label:before {
          color: lookup(theme + '-form-error-color')
        }
      }

      &.is-error {
        & .x-input,
        .x-select__trigger,
        .x-cascader__trigger {
          border-color: lookup(theme + '-form-error-color')

          &:hover,
          &:focus {
            border-color: lookup(theme + '-form-error-color')
          }
        }
      }



