.x-switch {
  display: inline-block;
  vertical-align: middle;
  height: 24px;
  border-radius: 24px;
  line-height: 22px;
  box-sizing: border-box;
  cursor: pointer;
  user-select: none;
  position: relative;
  font-size: 12px;
  padding-left: 20px;
  padding-right: 5px;
  text-align: center;
  transition: all 0.2s ease-in-out;
}
.x-switch i {
  width: 12px;
  height: 12px;
  text-align: center;
}
.x-switch__button {
  content: '';
  width: 20px;
  height: 20px;
  border-radius: 20px;
  position: absolute;
  left: 2px;
  top: 2px;
  cursor: pointer;
  transition: left 0.2s ease-in-out;
}
.x-switch--small {
  height: 16px;
  line-height: 10px;
}
.x-switch--small .x-switch__button {
  width: 12px;
  height: 12px;
}
.x-switch--checked {
  padding-right: 20px;
  padding-left: 5px;
}
.x-switch--disabled {
  cursor: not-allowed;
}
.x-switch--disabled .x-switch__button {
  cursor: not-allowed;
}
.theme-light .x-switch {
  background-color: #ccc;
  color: #fff;
}
.theme-light .x-switch__button {
  background-color: #fff;
}
.theme-light .x-switch--disabled {
  background: #e5e5e5;
  border-color: #e5e5e5;
