.x-table-root
  width 100%
  height auto
  min-height 20px
  position relative
  overflow hidden

  .x-table-container
    position relative
    border-width 1px
    border-style solid
    box-sizing border-box
    width 100%
    max-height 2000px
    .table-header-container
    .table-body-container
      width 100%
      height 100%
      box-sizing border-box

  .table
    table-layout fixed
    th
    td
      text-align left
      position relative
      .cell
        padding 5px 10px
    tr:not(:first-child)
      border-top-width 1px
      border-top-style solid
    th:not(:first-child)
    td:not(:first-child)
      border-left-width 1px
      border-left-style solid

    .checkbox
      margin-right 0
      width 100%
      text-align center
      & > span
        margin-right 0

  .header
    width 100%
    height auto
    border 1px solid
    border-bottom 0
    box-sizing border-box
    .table-header-left-box
      float left
    .table-header-right-box
      float right

  .footer
    width 100%
    height auto
    margin-top 15px
    .table-footer-left-box
      float left
    .table-footer-right-box
      float right

for theme in themes
  .theme-{theme}
    .x-table-root
      background lookup(theme + '-table-background-color')
      .default-border-color
        border-color lookup(theme + '-table-border-color')
      table.table th
        background lookup(theme + '-table-th-background-color')
      .table.body-table tr:not(.no-data-tips-box):hover
        background lookup(theme + '-table-tr-hover-background-color')
      &.zebra-line .table tr:nth-child(2n + 1)
        background lookup(theme + '-table-zebra-line-background-color')




