.x-slider-component-root
  position relative
  width 100%
  padding 1px
  box-sizing border-box
  display inline-block
  transition all ease .2s
  &.hide
    opacity 0
  .not-allowed
    cursor not-allowed
  .slider
    position relative
    width 100%
    box-sizing border-box
    opacity 1
    /*&:hover
      opacity 1*/
    .line-box
      position relative
      display inline-block
      width 100%
      height 4px
      top -2px
    .line
      position absolute
      width 100%
      height 0px
      border-top 4px solid
    .circle
      position absolute
      top 0
      left 0
      border 2px solid
      border-radius 100px
      box-sizing border-box
      &.focus
        box-shadow 0 0 5px 0px
      &:hover
        transform scale(1.5)
    .tooltip
      position absolute
      left 50%
      bottom 0
      width auto
      transform translate(-50%)
      background rgba(0,0,0,0.5)
      padding 10px 20px
      border-radius 5px
      font-size 12px
      font-weight 100
      text-align center
      white-space nowrap
      z-index zindex-tooltip
      .tooltip-input
        /*transform translate(10)*/
        margin 0
        padding 0
      .triangle
        position absolute
        top 100%
        left 50%
        transform translate(-50%)
        triangle(rgba(0,0,0,0.5), 10px)


  &>.label
    position absolute
    bottom 0
    &.min
      left 0
    &.max
      right 0

  .hidden-input
    position absolute
    top -5000px
    left -5000px
    width 1px
    height 1px
    opacity 0

  .pointer
    cursor pointer

  &.not-allowed
  .not-allowed
    cursor not-allowed
    .pointer
      cursor not-allowed

  .transition
    transition all ease-in-out 0.3s

.no-user-select
  user-select none

for theme in themes
  .theme-{theme}
    .x-slider-component-root
      .line.back-line
        color lookup(theme + '-back-line-color')
      .circle
        background lookup(theme + '-circle-background-color')
      .tooltip-input
        color lookup(theme + '-tooltip-input-color')
      &>.label
        color lookup(theme + '-label-color')
      .tooltip
        color lookup(theme + '-tooltip-color')




