













































































































































































































































































































































































































































































































































































@import '../../styles/common'

date-picker-cells-width = 196px
time-picker-cells-width-base = 56px
time-picker-cells-width-with-date-base = 72px
time-picker-cells-width = time-picker-cells-width-base * 2
time-picker-cells-width-with-seconds = time-picker-cells-width-base *3
time-picker-cells-width-with-date = time-picker-cells-width-with-date-base * 2
time-picker-cells-width-with-date-with-seconds = time-picker-cells-width-with-date-base * 3

.x-date-picker
  /*position relative*/
  line-height normal
  font-size font-size-small !important

  &__rel
    position relative

  .x-select-dropdown
    width auto
    padding 0
    overflow visible
    max-height none

  &-cells
    width date-picker-cells-width
    margin 10px

    span
      display inline-block
      size 24px

      em
        display inline-block
        size 24px
        line-height 24px
        margin 2px
        font-style normal
        border-radius btn-border-radius-small
        text-align center
        transition all transition-time ease-in-out

    &__header span
      line-height 24px
      text-align center
      margin 2px
    &__cell
      span&
        size 28px
        cursor pointer
      span&--disabled,
      span&--disabled:hover
        cursor not-allowed
      &--today
        em
          position relative
          &:after
            absolute top 1px right 1px
            content ''
            display block
            size 6px
            border-radius 50%
      &--range
        position relative

        em
          position relative
          z-index 1

        &:before
          absolute left top 2px right bottom 2px
          content ''
          display block
          border-radius 0
          border 0
  &-cells--year,
  &-cells--month
    margin-top 14px

    span
      size 40px 28px
      line-height 28px
      margin 10px 12px
      border-radius btn-border-radius-small

      em
        size 40px 28px
        line-height 28px
        margin 0

  &-header
    height 32px
    line-height 32px
    text-align center

    &-label
      cursor pointer
      transition color transition-time ease-in-out

  &__prev-btn
    float left

    &--arrow-double
      margin-left 10px

  &__next-btn
    float right

    &--arrow-double
      margin-right 10px

  &--with-range
    .x-picker-panel
      &-body
        min-width (date-picker-cells-width + 20) * 2
      &-content
        float left

.x-picker
  &-panel
    &__icon-btn
      display inline-block
      size 20px 24px
      line-height 26px
      margin-top 4px
      text-align center
      cursor pointer
      transition color transition-time ease-in-out

      i
        font-size 14px

    &-body-wrapper&--with-sidebar
      padding-left 92px

    &-sidebar
      float left
      width 92px
      margin-left -92px
      absolute top bottom
      border-radius border-radius-small 0 0 border-radius-small
      overflow auto

    &-shortcut
      padding btn-padding-base
      transition all transition-time ease-in-out
      cursor pointer
      overflow hidden
      text-overflow ellipsis
      white-space nowrap

    &-body
      float left

  &-confirm
    text-align right
    padding 8px
    clear both

    & > span
      cursor pointer
      user-select none
      float left
      padding 2px 0
      transition all transition-time ease-in-out

    & > span&--time-disabled
      cursor not-allowed

.x-time-picker
  &-cells
    min-width time-picker-cells-width

    &--with-seconds
      min-width time-picker-cells-width-with-seconds

    &__list
      width time-picker-cells-width-base
      max-height 144px
      float left
      overflow hidden
      position relative
      box-sizing border-box

      &:hover
        overflow-y auto

      &:first-child
        border-left none
        border-radius btn-border-radius-small 0 0 btn-border-radius-small

      &:last-child
        border-radius 0 btn-border-radius-small btn-border-radius-small 0

      ul
        width 100%
        margin 0
        padding 0 0 120px 0
        list-style none
        box-sizing border-box

        li
          size 100% 24px
          line-height 24px
          margin 0
          padding 0 0 0 15px
          box-sizing content-box
          text-align left
          cursor pointer
          user-select none
          list-style none
          transition background transition-time ease-in-out

    &__cell
      &--disabled
        cursor not-allowed
        &:hover
          cursor not-allowed

  &-header
    height 32px
    line-height 32px
    text-align center

  &--with-range
    .x-picker-panel
      &-body
        min-width (time-picker-cells-width * 2 + 4px)
      &-content
        float left
        position relative

        &:after
          content ''
          display inline-block
          width 2px
          absolute top 31px bottom right -2px
          z-index 1

        &--right
          float right
          &:after
            right auto
            left -2px
    .x-picker-cells
      &__list
        &:first-child
          border-radius 0
        &:last-child
          border-radius 0

  &--with-range&--with-seconds
    .x-picker-panel
      &-body
        min-width (time-picker-cells-width-with-seconds * 2 + 4px)

.x-picker-panel-content
  .x-picker-panel-content
    .x-time-picker
      &-cells
        min-width time-picker-cells-width-with-date-with-seconds

        &--with-seconds
          min-width time-picker-cells-width-with-date-with-seconds

          .x-time-picker-cells__list
            width (time-picker-cells-width-with-date-with-seconds / 3)
            ul
              li
                padding 0 0 0 28px
        &__list
          width (time-picker-cells-width-with-date-with-seconds / 2)
          max-height 216px

          &:first-child
            border-radius 0

          &:last-child
            border-radius 0

          ul
            padding 0 0 192px 0

            li
              padding 0 0 0 46px

for theme in themes
  .theme-{theme}
    .x-date-picker
      .x-date-picker-cells
        .x-date-picker-cells__header span
          color lookup(theme + '-date-picker-header-color')
        .x-date-picker-cells__cell:hover
          em
            background lookup(theme + '-date-picker-cell-active-bg')
        .x-date-picker-cells__cell--prev-month,
        .x-date-picker-cells__cell--next-month
          em
            color lookup(theme + '-date-picker-operation-color')
          &:hover
            em
              background transparent
        span.x-date-picker-cells__cell--disabled,
        span.x-date-picker-cells__cell--disabled:hover
          background lookup(theme + '-date-picker-disabled-bg')
          color lookup(theme + '-date-picker-disabled-color')
          em
            color inherit
            background inherit
        .x-date-picker-cells__cell--today
          em
            &:after
              background lookup(theme + '-date-picker-today-bg')
        .x-date-picker-cells__cell--range
          &:before
            background lookup(theme + '-date-picker-cell-active-bg')
        .x-date-picker-cells__cell--selected,
        .x-date-picker-cells__cell--selected:hover
          em
            background lookup(theme + '-date-picker-selected-bg')
            color lookup(theme + '-date-picker-selected-color')
        span.x-date-picker-cells__cell--disabled.x-date-picker-cells__cell--selected
          em
            background lookup(theme + '-date-picker-disabled-bg')
            color lookup(theme + '-date-picker-disabled-color')
        .x-date-picker-cells__cell--today.x-date-picker-cells__cell--selected
          em
            &:after
              background lookup(theme + '-date-picker-selected-color')
      .x-date-picker-header
        border-bottom 1px solid lookup(theme + '-date-picker-border-color')

      .x-date-picker-header-label
        &:hover
          color lookup(theme + '-date-picker-hover-color')

    .x-picker
      &-panel
        &__icon-btn
          color lookup(theme + '-date-picker-disabled-color')
          &:hover
            color lookup(theme + '-date-picker-hover-color')

        &-sidebar
          background lookup(theme + '-date-picker-sidebar-bg')
          border-right 1px solid lookup(theme + '-date-picker-border-color')

        &-shortcut
          &:hover
            background lookup(theme + '-date-picker-shortcut-hover-bg')

      &-confirm
        border-top 1px solid lookup(theme + '-date-picker-border-color')

        & > span
          color ookup(theme + '-date-picker-confirm-color')

          &:hover
            color ookup(theme + '-date-picker-confirm-hover-color')

          &:active
            color ookup(theme + '-date-picker-confirm-hover-color')

        & > span&--time-disabled
          color lookup(theme + '-date-picker-disabled-color')

    .x-time-picker
      &-cells
        &__list
          border-left 1px solid lookup(theme + '-time-picker-border-color')

        &__cell
          &:hover
            background lookup(theme + '-time-picker-disabled-bg')
          &--disabled
            color lookup(theme + '-time-picker-disabled-color')

            &:hover
              color lookup(theme + '-time-picker-disabled-color')
              background lookup(theme + '-time-picker-disabled-hover-bg')
          &--selected,
          &--selected:hover
            color lookup(theme + '-time-picker-selected-color')
            background lookup(theme + '-time-picker-disabled-bg')

      &-header
        border-bottom 1px solid lookup(theme + '-time-picker-border-color')

      &--with-range
        .x-picker-panel
          &-content
            &:after
              background lookup(theme + '-time-picker-border-color')



