

































































































@import '../../styles/common'

for theme in themes
  .theme-{theme}
    .x-radio-group--button
      .x-radio-wrapper
        color lookup(theme + '-radio-group-color')
        border 1px solid lookup(theme + '-radio-group-border-color')
        border-left 0
        background lookup(theme + '-radio-group-bg')

        &:first-child
          border-left 1px solid lookup(theme + '-radio-group-border-color')

        &:before
          background lookup(theme + '-radio-group-border-color')

        &:hover
          color lookup(theme + '-radio-group-hover-color')

            // 
        &--checked
          background lookup(theme + '-radio-group-checked-bg')
          border-color lookup(theme + '-radio-group-checked-border-color')
          color lookup(theme + '-radio-group-checked-color')
          box-shadow -1px 0 0 0 lookup(theme + '-radio-group-checked-border-color')

          &:first-child
            border-color lookup(theme + '-radio-group-checked-border-color')
            box-shadow none !important

          &:hover
            border-color lookup(theme + '-radio-group-checked-hover-border-color')
            box-shadow -1px 0 0 0 lookup(theme + '-radio-group-checked-hover-border-color')
            color lookup(theme + '-radio-group-checked-hover-color')

          &:active
            border-color lookup(theme + '-radio-group-checked-hover-active-color')
            box-shadow -1px 0 0 0 lookup(theme + '-radio-group-checked-active-border-color')
            color lookup(theme + '-radio-group-checked-active-color')

        // 
        &--disabled
          border-color lookup(theme + '-radio-group-disabled-border-color')
          background-color lookup(theme + '-radio-group-disabled-bg')
          cursor not-allowed
          color lookup(theme + '-radio-group-disabled-color')

          &:first-child,
          &:hover
            border-color lookup(theme + '-radio-group-disabled-hover-border-color')
            background-color lookup(theme + '-radio-group-disabled-hover-bg')
            color lookup(theme + '-radio-group-disabled-hover-color')
          &:first-child
            border-left-color lookup(theme + '-radio-group-disabled-border-color')

        &--disabled.x-radio-wrapper--checked
          color lookup(theme + '-radio-group-disabled-checked-color')
          background-color lookup(theme + '-radio-group-disabled-checked-bg')
          border-color lookup(theme + '-radio-group-disabled-checked-border-color')
          box-shadow none !important

// 
.x-radio-group
  display inline-block
  font-size font-size-small

  &--vertical
    .x-radio-wrapper
      display block
      height 30px
      line-height 30px

// 
.x-radio-group--button
  font-size 0

  .x-radio
    width 0
    margin-right 0

  .x-radio-wrapper
    display inline-block
    height btn-height-base
    line-height btn-height-base - 2px
    margin 0
    padding 0 10px
    font-size font-size-small
    transition all transition-time ease-in-out
    cursor pointer
    box-sizing border-box

    & > span
      margin-left 0

    &:before
      absolute left -1px
      size 1px 100%
      content ' '
      visibility hidden
      transition all transition-time ease-in-out

    // 
    &:first-child
      border-radius btn-border-radius-base 0 0 btn-border-radius-base

      &:before
        display none

    // 
    &:last-child
      border-radius 0 btn-border-radius-base btn-border-radius-base 0

    // 
    &:first-child:last-child
      border-radius btn-border-radius-base

    &:hover
      position relative

    // 
    .x-radio-inner,
    input
      opacity 0
      size 0

.x-radio-group
  &--small
    .x-radio-wrapper
      height btn-height-base - 4px
      line-height btn-height-base - 4px - 2px
      padding 0 8px
      font-size btn-font-size-small
  &--large
    .x-radio-wrapper
      height btn-height-base + 4px
      line-height btn-height-base + 4px - 2px
      padding 0 12px
      font-size btn-font-size-large




