}
span.x-date-picker-cells__cell--disabled,
span.x-date-picker-cells__cell--disabled:hover {
  cursor: not-allowed;
}
.x-date-picker-cells__cell--today em {
  position: relative;
}
.x-date-picker-cells__cell--today em:after {
  position: absolute;
  top: 1px;
  right: 1px;
  content: '';
  display: block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
}
.x-date-picker-cells__cell--range {
  position: relative;
}
.x-date-picker-cells__cell--range em {
  position: relative;
  z-index: 1;
}
.x-date-picker-cells__cell--range:before {
  position: absolute;
  left: 0;
  top: 2px;
  right: 0;
  bottom: 2px;
  content: '';
  display: block;
  border-radius: 0;
  border: 0;
}
