






































































































@import '../../styles/common'
@import '../../styles/mixins/select'

select-item('x-select', 'x-select-item')

for theme in themes
  .theme-{theme}
    select-item-color(theme, 'x-select', 'x-select-item')

    .x-select--multiple .x-select-item
      &--selected
        color lookup(theme + '-select-multiple-item-selected-color')
        background lookup(theme + '-select-multiple-item-selected-bg')
      &--focus, &:hover
        background lookup(theme + '-select-multiple-item-hover-bg')
      &--selected:after
        color lookup(theme + '-select-multiple-item-selected-icon-color')

.x-select-item .after
  display none
.x-select--multiple .x-select-item
  &--selected
    .after
      display inline-block
      float right



