.x-dropdown {
  display: inline-block;
}
.x-dropdown .x-select-dropdown {
  width: auto;
  overflow: visible;
  max-height: none;
}
.x-dropdown .x-dropdown {
  width: 100%;
}
.x-dropdown__rel {
  display: inline-block;
  position: relative;
}
.x-dropdown__menu {
  min-width: 80px;
}
.x-dropdown-item {
  margin: 0;
  padding: 5px 15px;
  clear: both;
  font-size: 12px !important;
  white-space: nowrap;
  list-style: none;
  cursor: pointer;
  box-sizing: border-box;
  transition: background 0.2s ease-in-out;
}
.x-dropdown-item:hover {
  background: #f3f3f3;
}
.x-dropdown-item--focus {
  background: #f3f3f3;
}
.x-dropdown-item--disabled {
  cursor: not-allowed;
}
.x-dropdown-item--disabled:hover {
  cursor: not-allowed;
}
.x-dropdown-item--divided {
  margin-top: 5px;
  border-top: 1px solid #e5e5e5;
}
.x-dropdown-item--divided:before {
  content: '';
  height: 5px;
  display: block;
  margin: 0 -16px;
  background-color: #fff;
  position: relative;
  top: -7px;
}
.x-dropdown-large .x-dropdown-item {
  padding: 7px 16px 8px;
  font-size: 13px !important;
}
.theme-light .x-dropdown-item {
  color: #383838;
}
.theme-light .x-dropdown-item:hover {
  background: #f4f4f4;
}
.theme-light .x-dropdown-item--focus {
  background: #f4f4f4;
}
.theme-light .x-dropdown-item--disabled {
  color: #c3c3c3;
}
.theme-light .x-dropdown-item--disabled:hover {
  color: #c3c3c3;
  background-color: #f7f7f7;
}
.theme-light .x-dropdown-item--selected,
.theme-light .x-dropdown-item--selected:hover {
  color: #fff;
  background: #c0252e;
}
.theme-dark .x-dropdown-item {
  color: #999;
}
.theme-dark .x-dropdown-item:hover {
  background: #323232;
}
.theme-dark .x-dropdown-item--focus {
  background: #323232;
}
.theme-dark .x-dropdown-item--disabled {
  color: #8a8a8a;
}
.theme-dark .x-dropdown-item--disabled:hover {
  color: #8a8a8a;
  background-color: #383838;
}
.theme-dark .x-dropdown-item--selected,
.theme-dark .x-dropdown-item--selected:hover {
  color: #fff;
  background: #c0252e;
}
/*# sourceMappingURL=src/components/dropdown/dropdown.css.map */


