.x-table-root {
  width: 100%;
  height: auto;
  min-height: 20px;
  position: relative;
  overflow: hidden;
}
.x-table-root .x-table-container {
  position: relative;
  border-width: 1px;
  border-style: solid;
  box-sizing: border-box;
  width: 100%;
  max-height: 2000px;
}
.x-table-root .x-table-container .table-header-container,
.x-table-root .x-table-container .table-body-container {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.x-table-root .table {
  table-layout: fixed;
}
.x-table-root .table th,
.x-table-root .table td {
  text-align: left;
  position: relative;
}
.x-table-root .table th .cell,
.x-table-root .table td .cell {
  padding: 5px 10px;
}
.x-table-root .table tr:not(:first-child) {
  border-top-width: 1px;
  border-top-style: solid;
}
.x-table-root .table th:not(:first-child),
.x-table-root .table td:not(:first-child) {
  border-left-width: 1px;
  border-left-style: solid;
}
.x-table-root .table .checkbox {
  margin-right: 0;
  width: 100%;
  text-align: center;
}
.x-table-root .table .checkbox > span {
  margin-right: 0;
}
.x-table-root .header {
  width: 100%;
  height: auto;
  border: 1px solid;
  border-bottom: 0;
  box-sizing: border-box;
}
.x-table-root .header .table-header-left-box {
  float: left;
}
.x-table-root .header .table-header-right-box {
  float: right;
}
.x-table-root .footer {
  width: 100%;
  height: auto;
  margin-top: 15px;
}
.x-table-root .footer .table-footer-left-box {
  float: left;
}
.x-table-root .footer .table-footer-right-box {
  float: right;
}
.theme-light .x-table-root {
  background: #fff;
}
.theme-light .x-table-root .default-border-color {
  border-color: #dfe6ec;
}
.theme-light .x-table-root table.table th {
  background: #fafafa;
}
.theme-light .x-table-root .table.body-table tr:not(.no-data-tips-box):hover {
  background: #fafafa;
}
.theme-light .x-table-root.zebra-line .table tr:nth-child(2n + 1) {
  background: #fafafa;
}
.theme-dark .x-table-root {
  background: #141414;
}
.theme-dark .x-table-root .default-border-color {
  border-color: #323238;
}
.theme-dark .x-table-root table.table th {
  background: #2e2e2e;
}
.theme-dark .x-table-root .table.body-table tr:not(.no-data-tips-box):hover {
  background: #2e2e2e;
}
.theme-dark .x-table-root.zebra-line .table tr:nth-child(2n + 1) {
  background: #2e2e2e;
}
