.x-breadcrumb:before,
.x-breadcrumb:after {
  display: table;
  content: "";
  line-height: 0;
}
.x-breadcrumb:after {
  clear: both;
}
.x-breadcrumb {
  padding: 5px 15px 10px;
  line-height: 20px;
  font-size: 12px;
}
.x-breadcrumb a,
.x-breadcrumb span,
.x-breadcrumb i {
  display: inline-block;
}
.x-breadcrumb__back {
  margin-right: 5px;
  font-size: 16px;
}
.x-breadcrumb__link {
  margin-right: 5px;
}
.x-breadcrumb__seperator {
  margin-right: 5px;
}
.theme-light .x-breadcrumb__back {
  color: #999;
}
.theme-light .x-breadcrumb__back:hover {
  color: #666;
}
.theme-light .x-breadcrumb__link {
  color: #c0252e;
}
.theme-light .x-breadcrumb__seperator {
  color: #999;
}
.theme-dark .x-breadcrumb__back {
  color: #666;
}
.theme-dark .x-breadcrumb__back:hover {
  color: #999;
}
.theme-dark .x-breadcrumb__link {
  color: #c0252e;
}
.theme-dark .x-breadcrumb__seperator {
  color: #666;
}
