.x-alert:before,
.x-alert:after {
  display: table;
  content: "";
  line-height: 0;
}
.x-alert:after {
  clear: both;
}
.x-alert {
  display: table;
  width: 100%;
  box-sizing: border-box;
  position: relative;
  padding: 6px 48px 6px 15px;
  border-radius: 2px;
  font-size: 12px;
  margin-bottom: 10px;
}
.x-alert-message-wrap {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
}
.x-alert__icon {
  font-size: 20px;
  float: left;
  line-height: 1;
  width: 20px;
  margin-right: 10px;
}
.x-alert__desc {
  font-size: 12px;
}
.x-alert__close {
  font-size: 12px;
  position: absolute;
  top: 8px;
  right: 15px;
  overflow: hidden;
  cursor: pointer;
}
.x-alert--with-desc {
  padding: 15px;
  position: relative;
  border-radius: 2px;
  margin-bottom: 10px;
  line-height: 1.5;
}
.x-alert--with-desc .x-alert__icon {
  width: 32px;
}
.x-alert--with-desc.x-alert--with-icon {
  padding: 15px 48px 15px 15px;
}
.x-alert--with-desc .x-alert__message {
  font-size: 14px;
  display: block;
}
.x-alert--with-desc .x-alert__icon {
  font-size: 32px;
}
.x-alert--large {
  padding: 15px 48px 15px 15px;
}
.x-alert--large .x-alert__message {
  font-size: 18px;
}
.x-alert--large .x-alert__desc {
  font-size: 14px;
}
.x-alert--large .x-alert__icon {
  width: 40px;
  font-size: 40px;
}
.x-alert--naked {
  padding: 8px 20px;
}
.x-alert--inline {
  display: inline-block;
  width: auto;
}
.x-alert--inline .x-alert-message-wrap {
  width: auto;
}
.x-alert--inline .x-alert__icon {
  position: static;
  vertical-align: middle;
}
.x-alert--inline .x-alert__message {
  vertical-align: middle;
}
.theme-light .x-alert {
  color: #383838;
}
.theme-light .x-alert__desc {
  color: #383838;
}
.theme-light .x-alert--info {
  border: 1px solid #dce9f4;
  background-color: #f6f9fc;
}
.theme-light .x-alert--info .x-alert__icon {
  color: #5393cb;
}
.theme-light .x-alert--success {
  border: 1px solid #cceacc;
  background-color: #f2f9f2;
}
.theme-light .x-alert--success .x-alert__icon {
  color: #090;
}
.theme-light .x-alert--warning {
  border: 1px solid #ffe0cc;
  background-color: #fff7f2;
}
.theme-light .x-alert--warning .x-alert__icon {
  color: #f60;
}
.theme-light .x-alert--error {
  border: 1px solid #f2d3d5;
  background-color: #fbf4f4;
}
.theme-light .x-alert--error .x-alert__icon {
  color: #c0252e;
}
.theme-light .x-alert__close .icon-times {
  font-size: 14px;
  color: #999;
  transition: color  ease;
  position: relative;
  top: -1px;
}
.theme-light .x-alert__close .icon-times:hover {
  color: #666;
}
.theme-light .x-alert--with-desc {
  color: #383838;
}
.theme-light .x-alert--with-desc .theme-light .x-alert__message {
  color: #383838;
}
.theme-light .x-alert--naked {
  border-color: transparent;
  background-color: transparent;
}
.theme-dark .x-alert {
  color: #999;
}
.theme-dark .x-alert__desc {
  color: #999;
}
.theme-dark .x-alert--info {
  border: 1px solid #212121;
  background-color: #212121;
}
.theme-dark .x-alert--info .x-alert__icon {
  color: #5393cb;
}
.theme-dark .x-alert--success {
  border: 1px solid #212121;
  background-color: #212121;
}
.theme-dark .x-alert--success .x-alert__icon {
  color: #090;
}
.theme-dark .x-alert--warning {
  border: 1px solid #212121;
  background-color: #212121;
}
.theme-dark .x-alert--warning .x-alert__icon {
  color: #f60;
}
.theme-dark .x-alert--error {
  border: 1px solid #212121;
  background-color: #212121;
}
.theme-dark .x-alert--error .x-alert__icon {
  color: #c0252e;
}
.theme-dark .x-alert__close .icon-times {
  font-size: 14px;
  color: #666;
  transition: color  ease;
  position: relative;
  top: -1px;
}
.theme-dark .x-alert__close .icon-times:hover {
  color: #999;
}
.theme-dark .x-alert--with-desc {
  color: #999;
}
.theme-dark .x-alert--with-desc .theme-dark .x-alert__message {
  color: #999;
}
.theme-dark .x-alert--naked {
  border-color: transparent;
  background-color: transparent;
}
