.x-checkbox-wrapper {
  font-size: 12px;
  vertical-align: middle;
  display: inline-block;
  position: relative;
  white-space: nowrap;
  margin-right: 8px;
  cursor: pointer;
}
.x-checkbox-wrapper--disabled {
  cursor: not-allowed;
}
.x-checkbox-wrapper + span,
.x-checkbox + span {
  margin-right: 4px;
}
.x-checkbox {
  display: inline-block;
  margin-right: 4px;
  white-space: nowrap;
  outline: none;
  position: relative;
  line-height: 1;
  vertical-align: middle;
  cursor: pointer;
}
.x-checkbox-inner {
  display: inline-block;
  width: 14px;
  height: 14px;
  position: relative;
  top: 0;
  left: 0;
  border-radius: 2px;
  border: 1px solid;
  transition: all 0.2s ease-in-out;
  box-sizing: border-box;
}
.x-checkbox-inner:after {
  position: absolute;
  left: 4px;
  top: 1px;
  content: ' ';
  width: 4px;
  height: 8px;
  display: table;
  border: 2px solid;
  border-top: 0;
  border-left: 0;
  transform: rotate(45deg) scale(0);
  transition: all 0.2s ease-in-out;
  box-sizing: border-box;
}
.x-checkbox-input {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  opacity: 0;
  cursor: pointer;
}
.x-checkbox--checked .x-checkbox-inner:after {
  transform: rotate(45deg) scale(1);
}
.x-checkbox--disabled.x-checkbox--checked .x-checkbox-inner:after {
  animation-name: none;
}
.x-checkbox--disabled .x-checkbox-inner {
  cursor: not-allowed;
}
.x-checkbox--disabled .x-checkbox-inner:after {
  animation-name: none;
}
.x-checkbox--disabled .x-checkbox-input {
  cursor: not-allowed;
}
.x-checkbox--disabled + span {
  cursor: not-allowed;
}
.x-checkbox--indeterminate .x-checkbox-inner:after {
  content: '';
  width: 8px;
  height: 1px;
  transform: scale(1);
  position: absolute;
  left: 2px;
  top: 5px;
}
.theme-light .x-checkbox:hover .x-checkbox-inner {
  border-color: #bcbcbc;
}
.theme-light .x-checkbox-inner {
  background-color: #fff;
  border-color: #dcdcdc;
}
.theme-light .x-checkbox-inner:after {
  border-color: #fff;
}
.theme-light .x-checkbox--checked:hover .x-checkbox-inner {
  border-color: #c0252e;
}
.theme-light .x-checkbox--checked .x-checkbox-inner {
  border-color: #c0252e;
  background-color: #c0252e;
}
.theme-light .x-checkbox--disabled.x-checkbox--checked:hover .x-checkbox-inner {
  border-color: #f3f3f3;
}
.theme-light .x-checkbox--disabled.x-checkbox--checked .x-checkbox-inner {
  background-color: #f3f3f3;
  border-color: #f3f3f3;
}
.theme-light .x-checkbox--disabled.x-checkbox--checked .x-checkbox-inner:after {
  border-color: #ccc;
}
.theme-light .x-checkbox--disabled + span {
  color: #ccc;
}
.theme-light .x-checkbox--indeterminate:hover .x-checkbox-inner {
  border-color: #c0252e;
}
.theme-light .x-checkbox--indeterminate .x-checkbox-inner {
  background-color: #c0252e;
  border-color: #c0252e;
}
.theme-dark .x-checkbox:hover .x-checkbox-inner {
  border-color: #555;
}
.theme-dark .x-checkbox-inner {
  background-color: #333;
  border-color: #444;
}
.theme-dark .x-checkbox-inner:after {
  border-color: #eee;
}
.theme-dark .x-checkbox--checked:hover .x-checkbox-inner {
  border-color: #c0252e;
}
.theme-dark .x-checkbox--checked .x-checkbox-inner {
  border-color: #c0252e;
  background-color: #c0252e;
}
.theme-dark .x-checkbox--disabled.x-checkbox--checked:hover .x-checkbox-inner {
  border-color: #444;
}
.theme-dark .x-checkbox--disabled.x-checkbox--checked .x-checkbox-inner {
  background-color: #444;
  border-color: #444;
}
.theme-dark .x-checkbox--disabled.x-checkbox--checked .x-checkbox-inner:after {
  border-color: #222;
}
.theme-dark .x-checkbox--disabled + span {
  color: #444;
}
.theme-dark .x-checkbox--indeterminate:hover .x-checkbox-inner {
  border-color: #c0252e;
}
.theme-dark .x-checkbox--indeterminate .x-checkbox-inner {
  background-color: #c0252e;
  border-color: #c0252e;
}
