.x-search-box
  display table
  width 100%
  position relative
  border-collapse seperate

  .x-search-box__select
  .x-search-box__input
  .x-search-box__button
    display table-cell

  .x-search-box__select
    min-width 90px
    width 90px
    margin-right -1px
    &.x-select--single
      .x-select__trigger
        border-bottom-right-radius 0
        border-top-right-radius 0

    & + .x-search-box__input
      .x-input
        border-radius 0
        border-left none

  .x-search-box__input
    min-width 140px
    .x-input
      border-right none

  .x-search-box__button
    min-width 32px
    width 32px
    padding-left 0
    padding-right 0
    border-bottom-left-radius 0
    border-top-left-radius 0



