.x-tree-1
  width 100%
  height 100%
  overflow auto
  position relative
  .list
    /*height 32px
    line-height 32px*/
    .tree-content
      position relative
      padding-left 20px
      box-sizing border-box
      .tree-content-label
        white-space nowrap
      span,i
        cursor pointer
      .edit-input
        position absolute
        left 20px
        width 100px
      .line
        position absolute
        height 0
        border-top 1px solid #aaa
        top 50%
        transform translate(0, -1px)
      .vertical-line
        position absolute
        width 0
        border-left 1px solid #aaa
        height 10px
        bottom 50%
    &.selected
      background #f3f3f3
      .icon
        background #f3f3f3
  .icon
    width 10px
    height 15px
    background #fff
    position absolute
    left 0
    top 50%
    transform translate(0, -50%)
    z-index 1
  .hide-input
    width 100%
    height 100%
    opacity 0.3
    position absolute
    left -9999px



