.x-tabs {
  padding: 0 15px;
  border-bottom-width: 1px;
  border-bottom-style: solid;
}
.x-tabs ul {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 0;
}
.x-tabs li {
  display: inline-block;
  font-size: 13px;
  padding: 0 20px;
  line-height: 28px;
  cursor: pointer;
  box-sizing: border-box;
}
.x-tabs li:hover {
  text-decoration: none;
}
.x-tabs li.router-link-active {
  height: 29px;
  border-width: 1px;
  border-style: solid;
  border-bottom: none !important;
  position: relative;
  bottom: -1px;
  font-weight: bold;
}
.x-tabs--line li.router-link-active {
  border: none;
}
.x-tabs--line li.router-link-active:after {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 3px;
  content: '';
}
.theme-light .x-tabs {
  border-bottom-color: #dcdcdc;
}
.theme-light .x-tabs li {
  color: #666;
}
.theme-light .x-tabs li:hover {
  color: #c0252e;
}
.theme-light .x-tabs li.router-link-active {
  background-color: #fff;
  border-color: #dcdcdc;
}
.theme-light .x-tabs--line li.router-link-active:after {
  background-color: #c0252e;
}
.theme-dark .x-tabs {
  border-bottom-color: #323238;
}
.theme-dark .x-tabs li {
  color: #999;
}
.theme-dark .x-tabs li:hover {
  color: #c0252e;
}
.theme-dark .x-tabs li.router-link-active {
  background-color: #141414;
  border-color: #323238;
}
.theme-dark .x-tabs--line li.router-link-active:after {
  background-color: #c0252e;
}
/*# sourceMappingURL=src/components/tabs/tabs.css.map */


