.x-upload-root {
  position: relative;
  display: inline-block;
}
.x-upload-root .input-file {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
.x-upload-root .progress-box {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 6px;
  line-height: 0;
}
.x-upload-root .upload-button-box {
  width: 100px;
  height: 100px;
  line-height: 100px;
  box-sizing: border-box;
  border: 1px dashed;
  text-align: center;
  font-size: 25px;
  color: #aaa;
  transition: all ease 0.3s;
}
.x-upload-root .upload-button-box:hover {
  background: rgba(0,0,0,0.02);
}
.x-upload-root .delete-button {
  position: absolute;
  top: -10px;
  right: -10px;
  width: 20px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  font-size: 15px;
  background: #fff;
  border-radius: 100px;
  box-shadow: 0 1px 2px rgba(0,0,0,0.2);
  color: #c0252e;
  cursor: pointer;
/*&>.icon
      border-radius 100px
      font-size 20px*/
}
/*# sourceMappingURL=src/components/upload/upload.css.map */


