@import '../../styles/mixins/select'

.x-dropdown
  display inline-block

  .x-select-dropdown
    width auto
    overflow visible
    max-height none

  .x-dropdown
    width 100%

  &__rel
    display inline-block
    position relative

  &__menu
    min-width 80px

select-item(x-dropdown, x-dropdown-item)

for theme in themes
  .theme-{theme}
    select-item-color(theme, 'x-dropdown', 'x-dropdown-item')



