
































































































































@import '../../styles/common'

for theme in themes
  .theme-{theme}
    .x-radio
      &:hover
        .x-radio-inner
          border-color lookup(theme + '-radio-hover-border-color')
      
      &-inner
        background-color lookup(theme + '-radio-bg')
        border 1px solid lookup(theme + '-radio-border-color')
      
        &:after
          background-color lookup(theme + '-radio-inner-bg')

    // 
    .x-radio--checked
      .x-radio-inner
        border-color lookup(theme + '-radio-checked-border-color')

      &:hover
        .x-radio-inner
          border-color lookup(theme + '-radio-checked-hover-border-color')

    // 
    .x-radio--disabled
      &:hover
        .x-radio-inner
          border-color lookup(theme + '-radio-disabled-hover-border-color')

      .x-radio-inner
        border-color lookup(theme + '-radio-disabled-border-color')
        background-color lookup(theme + '-radio-disabled-bg')

        &:after
          background-color lookup(theme + '-radio-disabled-inner-bg')

      .x-radio--disabled + span
        color lookup(theme + '-radio-disabled-inner-color')

// 
.x-radio-wrapper
  font-size font-size-small
  vertical-align middle
  display inline-block
  position relative
  white-space nowrap
  margin-right 8px
  cursor pointer

  &--disabled
    cursor not-allowed

.x-radio
  display inline-block
  margin-right 4px
  white-space nowrap
  outline none
  position relative
  line-height 1
  vertical-align middle
  cursor pointer

  &-inner
    display inline-block
    size 14px
    relative top left
    border-radius 50%
    transition all transition-time ease-in-out
    box-sizing border-box

    &:after
      absolute left 2px top 2px
      content ' '
      size 8px
      border-radius 50%
      display table
      border-top 0
      border-left 0
      opacity 0
      transition all transition-time ease-in-out
      transform scale(0)
      box-sizing border-box

  &-input
    absolute left top bottom right
    z-index 1
    opacity 0
    cursor pointer

// 
.x-radio--checked
  .x-radio-inner
    &:after
      opacity 1
      transform scale(1)

// 
.x-radio--disabled
  cursor not-allowed

  .x-radio-input
    cursor not-allowed

span.x-radio + *
  margin-left 2px
  margin-right 2px



