.validator-error-message-container
  position absolute
  top 100%
  left 0
  height 25px
  line-height 25px
  font-size 12px
  overflow hidden

for theme in themes
  .theme-{theme}
    .x-validator-root
      &.validator-default-style .validator-focus
        box-shadow lookup(theme + '-validator-focus-box-shadow')
      &.validator-default-style .validator-error
        box-shadow lookup(theme + '-validator-error-box-shadow')
    .validator-error-message-container
      color lookup(theme + '-validator-error-message-color')



