.validator-error-message-container {
  position: absolute;
  top: 100%;
  left: 0;
  height: 25px;
  line-height: 25px;
  font-size: 12px;
  overflow: hidden;
}
.theme-light .x-validator-root.validator-default-style .validator-focus {
  box-shadow: 0 0 1px 1px #20a0ff;
}
.theme-light .x-validator-root.validator-default-style .validator-error {
  box-shadow: 0 0 1px 1px #c0252e;
}
.theme-light .validator-error-message-container {
  color: #c0252e;
}
.theme-dark .x-validator-root.validator-default-style .validator-focus {
  box-shadow: 0 0 1px 1px #20a0ff;
}
.theme-dark .x-validator-root.validator-default-style .validator-error {
  box-shadow: 0 0 1px 1px #f00;
}
.theme-dark .validator-error-message-container {
  color: #f00;
}
